import Vue from "vue";
import Vuex from "vuex";

import {
	fetchNewMemeber,
	fetchBookCategory,
	fetchReadingList,
	fetchWeatherApi,
} from "../api/index.js";

Vue.use(Vuex);

const store = new Vuex.Store({
	state: {
		// title: "hello",
		commonLogo: {
			logo: require("@/assets/images/login-logo.png"),
		},
		// login 토큰 정보
		token: localStorage.getItem("AccessToken") || "",
		rToken: localStorage.getItem("RefreshToken") || "",

		// 로그인 유저 정보
		userId: "",

		// 유저정보
		arrUserInfo: [],

		// 특정 유저 정보
		arrOneUser: [],

		// easyday
		arrEzdayList: [],

		// easystory
		arrEzStoryList: [],

		// birthday
		arrBirthdayList: [],

		// board
		arrBoardList: [],

		// meetingroom
		arrMeetingRoom: [],

		// newMemver
		arrNewMember: [],

		// 미팅일정
		arrMeetingSchedule: [],

		// 신규도서 목록
		arrNewBook: [],

		// 도서 카테고리
		arrBookCategory: [],

		// 도서 검색 목록
		arrBookSearch: [],

		// 대여자
		arrReadingList: [],

		// mypage
		arrMypageList: [],
	},
	mutations: {
		setAccessToken(state, token) {
			state.token = token;
		},
		setRefreshToken(state, rToken) {
			state.rToken = rToken;
		},
		setUserId(state, userId) {
			state.userId = userId;
		},
		clearAccessToken(state) {
			console.log("clearAccessToken");
			state.token = "";
		},
		clearRefreshToken(state) {
			console.log("clearRefreshToken");
			state.rToken = "";
		},
		setWeatherInfo(state, data) {
			state.weatherInfo = data;
		},
		setUserInfo(state, data) {
			state.arrUserInfo = data;
		},
		setOneUser(state, data) {
			state.arrOneUser = data;
		},
		setEzDayList(state, listData) {
			state.arrEzdayList = listData;
		},
		setBirthdayList(state, listData) {
			state.arrBirthdayList = listData;
		},
		setEzStoryList(state, listData) {
			state.arrEzStoryList = listData;
		},
		// setUserList(state, userData) {
		// 	state.arrUserList = userData;
		// 	console.log(userData);
		// },
		setBoardList(state, data) {
			state.arrBoardList = data;
		},
		setNewMember(state, data) {
			state.arrNewMember = data;
		},
		setMeetingRoom(state, data) {
			state.arrMeetingRoom = data;
		},
		setMeetingSchedule(state, data) {
			state.arrMeetingSchedule = data;
		},
		setNewBook(state, data) {
			state.arrNewBook = data;
		},
		setBookCategory(state, data) {
			state.arrBookCategory = data;
		},
		setBookSearch(state, data) {
			state.arrBookSearch = data;
		},
		setReadingList(state, listdata) {
			state.arrReadingList = listdata;
		},
		setmypageList(state, data) {
			state.arrMypageList = data;
		},
	},
	actions: {
		// 신규입사자
		FETCH_MEMBER(context) {
			fetchNewMemeber(context)
				.then(response => {
					context.commit("setNewMember", response.data);
				})
				.catch(error => {
					console.log(error);
				});
		},
		// 대여자리스트
		FETCH_REDINGLIST(context) {
			fetchReadingList(context)
				.then(response => {
					context.commit("setReadingList", response.data);
				})
				.catch(error => {
					console.log(error);
				});
		},
		async FETCH_BOOKCATEGORY({ commit }) {
			try {
				const { data } = await fetchBookCategory();
				commit("setBookCategory", data);
			} catch ({ response }) {
				console.log(response);
			}
		},
	},
});

export default store;
