import Vue from "vue";
import VueRouter from "vue-router";
import store from "@/store/index";

Vue.use(VueRouter);

const router = new VueRouter({
	mode: "history",
	routes: [
		{
			path: "/",
			redirect: "/login",
		},
		{
			path: "/login",
			component: () => import("@/views/LoginPage.vue"),
		},
		{
			path: "/main",
			component: () => import("@/views/MainPage.vue"),
			meta: { auth: true },
		},
		{
			path: "/book",
			redirect: "/book/home",
			component: () => import("@/views/BookPage.vue"),
			meta: { auth: true },
			children: [
				{
					path: "/book/home",
					component: () => import("@/components/book/BookHome.vue"),
					meta: { auth: true },
				},
				{
					path: "/book/home/:sq",
					component: () => import("@/components/book/BookSearch.vue"),
					meta: { auth: true },
				},
				{
					path: "/book/category/",
					component: () => import("@/components/book/BookCategory.vue"),
					meta: { auth: true },
				},
			],
		},
		{
			path: "/contact",
			component: () => import("@/views/ContactPage.vue"),
			meta: { auth: true },
		},
		{
			path: "/mypage",
			component: () => import("@/views/MyPage.vue"),
			meta: { auth: true },
		},
		{
			path: "/User/:id",
			component: () => import("@/views/ProfilePage.vue"),
			meta: { auth: true },
		},
		{
			path: "*",
			component: () => import("@/views/NotFound.vue"),
		},
	],
});

router.beforeEach((to, from, next) => {
	// console.log(to);
	// console.log(from);
	if (to.meta.auth && store.state.token == "") {
		alert("페이지 접근 권한이 없습니다.");
		next("/login");
		return;
	}
	if (to.path === "/login" && store.state.token != "") {
		next("/main");
		return;
	}
	next();
});

export default router;
