import router from "@/router/index";
import store from "@/store/index";
import axios from "axios";
import { getstartEndDay } from "./datecalc";
// import { contactData } from "../components/contactForm.vue";
import moment from "moment";
export const instance = axios.create({
	baseURL: "https://mattyapi.easymedia.co.kr/",
});

export const weatherInstance = axios.create({
	baseURL: "https://api.openweathermap.org/data/2.5/",
});

export const instanceRefresh = axios.create({});

// 요청 인터셉터 추가하기
instance.interceptors.request.use(
	function (config) {
		// 요청이 전달되기 전에 작업 수행
		config.headers.token = store.state.token;
		return config;
	},
	function (error) {
		// 요청 오류가 있는 작업 수행
		console.log(error);
		return Promise.reject(error);
	},
);

// 응답 인터셉터 추가하기
instance.interceptors.response.use(
	function (response) {
		// 2xx 범위에 있는 상태 코드는 이 함수를 트리거 합니다.
		// 응답 데이터가 있는 작업
		return response;
	},
	async function (error) {
		// 2xx 외의 범위에 있는 상태 코드는 이 함수를 트리거 합니다.
		// 응답 오류가 있는 작업 수행

		store.commit("clearAccessToken");
		store.commit("clearRefreshToken");
		localStorage.clear();
		this.$router.push("/login");

		console.log(error.response.status);
		if (error.response.status === 403) {
			const refreshToken = localStorage.getItem("RefreshToken");
			// const { data } = await instance.post("api/TokenRefresh");
			// console.log(data);
			// console.log("VVVVVV");
		}

		return Promise.reject(error);
	},
);

// 로그인 계정 전송
function userLogin(userData) {
	return instance.post("api/Token", userData);
}
// Refresh 토근 발행
function generateRefreshToken() {
	return instance.get("api/TokenRefresh");
}

// 이지데이 fetch data
function fetchEzDay() {
	return instance.get("api/User", {
		params: {
			stype: "easyday",
			// start-end 0으로 다시 돌리기
			startdate: getstartEndDay(0).startD,
			enddate: getstartEndDay(0).endD,
		},
	});
}

// 생일자 fetch data
function fetchBirthday() {
	return instance.get("api/User", {
		params: {
			stype: "birthday",
			startdate: getstartEndDay(0).startD,
			enddate: getstartEndDay(0).endD,
		},
	});
}

//이지스토리 fetch data
function fetchEzStory() {
	return instance.get("api/EasyStory");
}

// 커뮤니티카테고리 불러오기
function fetchCommnunityCategory() {
	return instance.get("api/CommunityCategory");
}

// 카테고리 별 게시판 목록 불러오기
function fetchBoardList(category = "NOTICE") {
	return instance.get("api/Community", { params: { c: category, size: 5 } });
}

// 회의실 정보 불러오기
function fetchMeetingRoom() {
	return instance.get("api/MeetingRoom");
}

// 미팅일정 불러오기
function fetchMeetingSchedule() {
	const today = moment().format("YYYY-MM-DD");
	return instance.get("api/Meeting", {
		params: { date: today },
	});
}

// 신규입사자 목록 불러오기
function fetchNewMemeber() {
	return instance.get("api/User", {
		params: {
			stype: "new",
			startdate: getstartEndDay(-3).startD,
			enddate: getstartEndDay(-3).endD,
		},
	});
}

// 매티 유저 조회
function fetchUser() {
	return instance.get("api/User");
}

// 매티 특정 유저 조회
function fetchOneUser(user) {
	return instance.get(`api/User/${user}`);
}

// 도서목록 불러오기
function fetchBookList() {
	return instance.get("api/Book");
}

// 도서목록 불러오기(params)
function fetchBookListParams(treeid, paging = 1) {
	return instance.get("api/Book", {
		params: {
			c: treeid,
			p: paging,
		},
	});
}

// 도서 검색
function fetchBookSearch(keyword) {
	return instance.get("api/Book", {
		params: {
			field: "BOOK_TITLE",
			keyword,
			size: "80",
		},
	});
}

// 도서 카테고리 불러오기
function fetchBookCategory() {
	return instance.get("api/Category");
}

// 대야자 불러오기
function fetchReadingList() {
	return instance.get("api/Rent", {
		params: {
			m: -1,
		},
	});
}

// mypage 조회
function fetchMypage() {
	return instance.get("api/Vacation");
}

// 날씨 조회 api
function fetchWeatherApi() {
	navigator.geolocation.getCurrentPosition(position => {
		fetchWeatherInfo(position);
	});
	async function fetchWeatherInfo(position) {
		const apiKey = "ee5958b6c72ed307b4121794fb119bec";
		const lat = position.coords.latitude;
		const lon = position.coords.longitude;
		const url = await weatherInstance.get(
			`weather?lat=${lat}&lon=${lon}&lang=kr&appid=${apiKey}`,
		);
		console.log(url);
		return url;
		// return;
		// console.log(navigator.geolocation.getCurrentPosition(geoLoc));

		//  navigator.geolocation.getCurrentPosition(geoLoc);
	}
}
// fetchWeatherInfo();
export {
	userLogin,
	generateRefreshToken,
	fetchEzDay,
	fetchBirthday,
	fetchEzStory,
	fetchBoardList,
	fetchCommnunityCategory,
	fetchMeetingRoom,
	fetchMeetingSchedule,
	fetchNewMemeber,
	fetchUser,
	fetchOneUser,
	fetchBookList,
	fetchBookListParams,
	fetchBookSearch,
	fetchBookCategory,
	fetchReadingList,
	fetchMypage,
	fetchWeatherApi,
};
