import Vue from "vue";
import App from "./App.vue";
import router from "@/router/index";
import store from "@/store/index";
import SwiperPlugin from "./plugins/SwiperPlugin";
/* import font awesome icon component */
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { formatFullDate, formatFromMonth } from "@/utils/filters";
/* add font awesome icon component */
Vue.component("font-awesome-icon", FontAwesomeIcon);

Vue.config.productionTip = false;
Vue.use(SwiperPlugin);
Vue.filter("formatFullDate", formatFullDate);
Vue.filter("formatFromMonth", formatFromMonth);

new Vue({
	render: h => h(App),
	router,
	store,
}).$mount("#app");
