// 월 첫 날, 마지막 날 구하는 방법
export const getstartEndDay = function (month) {
	const now = new Date();
	const lastDate = `${new Date(
		now.getFullYear(),
		now.getMonth() + 1,
		0,
	).getDate()}`;
	return {
		startD: `${new Date().getFullYear()}-${
			new Date().getMonth() + month + 1
		}-01`,
		endD: `${new Date().getFullYear()}-${
			new Date().getMonth() + 1
		}-${lastDate}`,
	};
};
