<template>
	<div id="header">
		<div class="wrap">
			<div id="mainLogo">
				<img :src="`${$store.state.commonLogo.logo}`" alt="" />
			</div>
			<div class="weather_info">
				<swiper
					ref="weather_swipe"
					class="weather_swipe"
					:options="swiperOption"
				>
					<swiper-slide class="temp_info">
						<div class="weather_stat">
							<div class="img">
								<img
									:src="`https://openweathermap.org/img/wn/${arrWeather.desc.icon}.png`"
									alt=""
								/>
							</div>
							<div class="curr_temp">
								<span class="bold"
									>{{ arrWeather.main.temp || 0 | formatTemp }}&#176;</span
								>{{ arrWeather.desc.description }}
							</div>
							<p class="temp_min_max">
								<span class="min"
									>{{ arrWeather.main.temp_min || 0 | formatTemp }}&#176;</span
								><span class="max"
									>{{ arrWeather.main.temp_max || 0 | formatTemp }}&#176;</span
								>
							</p>
						</div>
					</swiper-slide>
					<swiper-slide class="air_info">
						<div class="air_stat">
							<div class="air air_2_5" :bind="PrintAir25Data()">
								<p class="txt">
									초미세 <span class="bold">{{ arrWeather.pm2_5 }}</span>
								</p>
								<div class="img" :class="arrWeather.pm2_5_img"></div>
							</div>
							<div class="air air_10" :bind="PrintAir10Data()">
								<p class="txt">
									미세 <span class="bold">{{ arrWeather.pm10 }}</span>
								</p>
								<div class="img" :class="arrWeather.pm10_img"></div>
							</div>
						</div>
					</swiper-slide>
				</swiper>
			</div>
		</div>
	</div>
</template>

<script>
import axios from "axios";
import { WeatherSwipe } from "@/mixins/mixins";
export default {
	mixins: [WeatherSwipe],
	data() {
		return {
			arrWeather: {
				desc: {},
				main: {},
				pm2_5: "",
				pm2_5_img: "",
				pm10: "",
				pm10_img: "",
			},
			airPollution: {},
		};
	},
	methods: {
		async getWeatherInfo(position) {
			const apiKey = "ee5958b6c72ed307b4121794fb119bec";
			const lat = position.coords.latitude;
			const lon = position.coords.longitude;
			try {
				const weatherData = await axios.get(
					"https://api.openweathermap.org/data/2.5/weather",
					{
						params: {
							lat,
							lon,
							lang: "kr",
							units: "metric",
							appid: apiKey,
						},
					},
				);
				const filtered = await weatherData.data.weather[0];
				this.arrWeather.desc = filtered;
				this.arrWeather.main = weatherData.data.main;
			} catch (error) {
				console.log(error);
			}
			try {
				const airPollution = await axios.get(
					"https://api.openweathermap.org/data/2.5/air_pollution",
					{
						params: {
							lat,
							lon,
							appid: apiKey,
						},
					},
				);
				const filtered = await airPollution.data.list[0].components;
				this.airPollution = filtered;
			} catch (error) {
				console.log(error);
			}
		},
		PrintAir25Data() {
			const list = {};
			if (this.airPollution.pm2_5 <= 15) {
				list.pm2_5 = "좋음";
				list.pm2_5_img = "good";
			} else if (
				this.airPollution.pm2_5 > 15 &&
				this.airPollution.pm2_5 <= 35
			) {
				list.pm2_5 = "보통";
				list.pm2_5_img = "neutral";
			} else if (
				this.airPollution.pm2_5 > 45 &&
				this.airPollution.pm2_5 <= 75
			) {
				list.pm2_5 = "나쁨";
				list.pm2_5_img = "bad";
			} else if (this.airPollution.pm2_5 > 75) {
				list.pm2_5 = "매우 나쁨";
				list.pm2_5_img = "worst";
			}
			this.arrWeather.pm2_5 = list.pm2_5;
			this.arrWeather.pm2_5_img = list.pm2_5_img;
		},
		PrintAir10Data() {
			const list = {};
			if (this.airPollution.pm10 <= 15) {
				list.pm10 = "좋음";
				list.pm10_img = "good";
			} else if (this.airPollution.pm10 > 15 && this.airPollution.pm10 <= 35) {
				list.pm10 = "보통";
				list.pm10_img = "neutral";
			} else if (this.airPollution.pm10 > 45 && this.airPollution.pm10 <= 75) {
				list.pm10 = "나쁨";
				list.pm10_img = "bad";
			} else if (this.airPollution.pm10 > 75) {
				list.pm10 = "매우 나쁨";
				list.pm10_img = "worst";
			}
			this.arrWeather.pm10 = list.pm10;
			this.arrWeather.pm10_img = list.pm10_img;
		},
	},
	// mounted() {
	// 	this.PrintAir25Data();
	// },
	filters: {
		formatTemp(value) {
			const temp = value;
			return Math.round(temp);
		},
	},
	created() {
		navigator.geolocation.getCurrentPosition(position => {
			this.getWeatherInfo(position);
			// this.PrintAir25Data();
		});
	},
};
</script>

